.modal-visible {
   overflow: hidden;
}

.modal-window {
   @include border-top-radius($global-radius);
   @include border-bottom-radius($global-radius);
   position: absolute;
   width: 90%;
   left: 5%;
   top: $base-spacing;
   @include lrswap(padding, $sm-spacing $sm-spacing $base-spacing);
   margin-bottom: $base-spacing;
   background: $lt-bg;
   overflow-x: auto;
   z-index: $modal + 1;

   &.ushahidi-logo {
      background: $white url("../img/ushahidi-logo.svg") no-repeat;
      background-size: 17%;
      background-position: 96% 2%;

      .divider {
         width: 80%;
      }
   }

   @include media($mobile) {
      padding: $base-spacing;
   }

   @include media($small) {
      top: $lg-spacing;
      width: 600px;
      margin-left: -300px;
      left: 50%;
   }

   input[type="text"],
   input[type="email"],
   input[type="password"],
   input[type="search"],
   textarea {
      //        width: 100%;
   }

   .modal-title {
      @extend .gamma;

      [class*="icon-"]:before {
         color: $mid-gray;
      }

      em {
         color: $dk-gray;
         font-weight: normal;
         font-style: normal;
      }
   }

   .modal-title,
   h3 {
      margin: $sm-spacing 0;

      &:before {
         color: $dk-gray;
         @include padding-right($sm-spacing);
      }

      &.settings {
         @include icon($before, settings){
            color: $dk-gray;
            @include padding-right($sm-spacing);
         }
      }
      &.edit {
         @include icon($before, pencil){
            color: $dk-gray;
            @include padding-right($sm-spacing);
         }
      }
      &.confirm {
         @include icon($before, check){
            color: $dk-gray;
            @include padding-right($sm-spacing);
         }
      }

   }

   .modal-body {

      @include media($small) {
         overflow-y: auto;
      }
   }

   .close {
      @include border-top-radius($global-radius);
      @include border-bottom-radius($global-radius);
      @include icon(before, close){
         font-size: em(18);
         padding: $sm-spacing;
      }
      position: absolute;
      top: $sm-spacing;
      @include right($sm-spacing);
      border: $base-border;

      @include media($mobile) {
         @include right($base-spacing);
         top: $base-spacing;
      }

      &:hover {
         border-color: $border-color-hover;
      }

   }

   .modal-submit {
      background: $lt-gray;
      @include lrswap(margin, $base-spacing (-$base-spacing) (-$base-spacing));
      @include lrswap(padding, $base-spacing $sm-spacing $base-spacing $base-spacing);

      .cta {
         @include padding-left($sm-spacing);
      }
   }

}

.modal-overlay {
   position: fixed;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   width: auto;
   height: auto;
   overflow: hidden;
   background: rgba(0, 0, 0, 0.75);
   z-index: $modal; //variable defined in settings.scss
}

.modal {
   display: none;

   .modal-window {
      @include transition(all 0.35s);
      opacity: 0;
      margin-top: -999px;
   }

   .modal-overlay {
      @include transition(all 0.2s);
      opacity: 0;
   }

   .modal-visible & {

      .modal-window {
         opacity: 1;
         margin-top: 0;
      }

      .modal-overlay {
         opacity: 1;
      }
   }
}
