.mode-bar {
    @include clearfix;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: $modebar;
    max-width: $lg-spacing*2;
    background-color: $color-primary;

    @include media($medium-down) {
        max-width: none;
        background-color: $color-primary-zeta;
    }

    ul {
        @extend .list-reset;
    }

    li {
        /*
        display: flex;
        flex-grow: 1;
        justify-content: space-around;
        */

        a {
            display: block;
            color: $color-tertiary;
            font-size: 12px;
            text-align: center;
            padding: $sm-spacing 2px;
            @include transition(0.3s);

            @include media($medium-down) {
                font-size: 11px;
                padding: $tiny-spacing/2 0;
            }

            svg.iconic {
                display: block;
                width: $sm-spacing;
                height: $sm-spacing;
                fill: $color-tertiary;
                margin: 4px auto 6px;
                @include transition(0.3s);
            }

            .avatar {
                width: $base-spacing;
                height: $base-spacing;
                margin: 4px auto 6px;
            }


            &:hover {
                color: $white;

                svg.iconic {
                    fill: $white;
                }
            }
        }

        &.more-menu-trigger {
            display: none;

            @include media($medium-down) {
                display: block;
            }
        }
    }

    .deployment-menu {

        @include media($medium-down) {
            position: absolute;
            right: 0;
            bottom: 0;
            left: 0;
        }

        li {

            &.persist {

                @include media($medium-down) {
                    display: block;
                    position: static;
                    @include float(left);
                    width: 25%;
                    list-style-type: none;
                    text-align: center;
                    background-color: $color-primary;
                }

                &.active {

                    a {
                        color: $color-secondary;

                        svg.iconic {
                            fill: $color-secondary;
                        }
                    }
                }
            }

            &.tuck {

                @include media($medium-down) {
                    display: none;
                    position: absolute;
                    right: 0;
                    bottom: 100%;
                    left: 0;

                    a {
                        color: $link-color;
                        @include typeBase;
                        text-align: left;
                        padding: $sm-spacing;

                        svg.iconic {
                            display: inline-block;
                            position: relative;
                            top: 2px;
                            fill: $link-color;
                            width: 1em;
                            height: 1em;
                            margin: 0;
                            margin-right: $tiny-spacing;
                        }
                    }
                }
            }
        }
    }

    .account-menu {
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -moz-flex;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column-reverse;
        flex-direction: column-reverse;

        @include media($medium-down) {
            display: none;
            padding-bottom: $huge-spacing*3;
        }

        a {
            padding: $tiny-spacing 2px;

            @include media($medium-down) {
                color: $link-color;
                @include typeBase;
                text-align: left;
                padding: $sm-spacing;

                &:hover {
                    color: $link-hover-color;

                    svg.iconic {
                        fill: $link-hover-color;
                    }
                }

                svg.iconic {
                    display: inline-block;
                    position: relative;
                    top: 2px;
                    fill: $link-color;
                    width: 1em;
                    height: 1em;
                    margin: 0;
                    margin-right: $tiny-spacing;
                }

                .avatar {
                    float: left;
                    width: 20px;
                    height: 20px;
                    margin: -3px 7px 0 -3px;
                }
            }

            @include media($medium) {
                position: relative;

                &:hover {

                    .label {
                        @extend .bug;
                        display: block;
                        position: absolute;
                        top: 0;
                        left: 100%;
                        margin-left: $tiny-spacing;
                    }
                }
            }
        }

        .label {

            @include media($medium) {
                display: none;
            }

            @include media($medium-down) {

                .badge {
                    display: inline-block;
                    height: $sm-spacing;
                    width: $sm-spacing;
                    color: $color-primary-alpha;
                    font-size: 10px;
                    font-weight: bold;
                    text-align: center;
                    line-height: 1.4;
                    white-space: nowrap;
                    vertical-align: middle;
                    @include equal-radius(100%);
                    background: $color-secondary;
                    @include lrswap(margin, 0 0 3px 8px);
                }
            }
        }
    }

    &.active {

        @include media($medium-down) {
            .account-menu {
                display: block;
            }

            .deployment-menu li {

                &.persist {

                    &.active {

                        a {
                            color: $color-tertiary;

                            svg.iconic {
                                fill: $color-tertiary;
                            }
                        }
                    }

                    &.more-menu-trigger {

                        a {
                            color: $color-secondary;

                            svg.iconic {
                                fill: $color-secondary;
                            }
                        }
                    }
                }

                &.tuck {
                    display: block;
                }
            }
        }
    }

    .main-nav {
        margin: 0;
        //Test if autoprefixer covers this
        display: -webkit-flex;
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: flex;

        @include media($medium) {
            display: block;
        }

        li {

            &:hover a,
            &.active a {
               color: $highlight;
            }

        }

        .ushahidi a {
            display: block;
            background: url('/assets/img/ushahidi-logo.png') no-repeat;
            background-size: 100%;
            height: 30px;
            width: 30px;
            margin: auto;
        }

        i {
            @include float(left);
            @include lrswap(padding, 0 $sm-spacing $sm-spacing 12px);
            font-size: em(20);
            transition: none;
        }

        span {
            @include float(left);
            clear: both;

            @include media($medium) {
                float: none;
                clear: none;
            }

        }

    }

    .dropdown-menu {

        @include media($medium) {
            min-width: inherit;
            border: none;
            box-shadow: none;
            background-color: transparent;
            margin-top: 0;
        }

        li:hover {
            background: transparent;
        }

    }

    .secondary-nav {
        margin: 0 0 40px 0;

        @include media($medium) {
            position: absolute;
            bottom: 0;
        }

        li {
            margin: 0;
            padding: 0;

            a {
                margin: 10px 20px;
                padding: 0;
                font-size: em(16);
            }

            &:hover a {
               color: $white;
            }

        }

        .notifications {

            a {
                @include icon(before, bell) {
                    font-size: em(16);
                }

            }

            &.active a:before {
                color: $highlight;
            }

        }

        .collections {

            a {
                @include icon(before, collection) {
                    font-size: em(16);
                }

            }

        }

        .account-settings {
            margin-top: 20px;

            a {
                width: 35px;
                margin: auto;
                @include margin-left(12px);
                padding: 0;
            }

        }

    }

    .more {

        @include media($medium) {
            display: none;
        }

    }

    .toggle-content {

        @include media($medium) {
            display: block;
            position: absolute;
            bottom: 0;
        }

    }

}
